import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { CategoryListComponent } from './category-list.component';
import { CategoryService } from 'src/app/service/category.service';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';

const routes: Routes = [
  {
    path: '',
    component: CategoryListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../category-add/category-add.module').then(m => m.CategoryAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../category-add/category-add.module').then(m => m.CategoryAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    CategoryListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    CategoryService
  ]
})
export class CategoryListModule { }
